<template>
  <div>
    <div class="top">
        <span @click="fanhui"><i class="iconfont icon-fanhui"></i></span>
        <h3>列表详情</h3>
    </div>
     <div class="list">
        <h3>{{xqlist.title}}</h3>
        <div class="zuozhe">
            <img :src="xqlist.avatar" alt=""/>
            <p>作者:{{xqlist.nickname}}</p>
            <p style="font-size:10px;">发布时间:{{ new Date(parseInt(xqlist.time)).toLocaleString().replace(/:\d{1,2}$/,'')}}</p>
           <button @click="dingyueyes" v-if="xqlist.is_fav">已收藏</button> 
           <button   @click="dingyue" v-else>+收藏</button>
            
        </div>
        <div class="myimg">
            <h6>{{xqlist.content}}</h6>
           <li> <img :src="item" alt="" v-for="item in xqlist.imageSrc"></li>
        </div>
    </div>
   <div class="footr">
    <span>评论：{{xqlist.comment}}</span>
    <span>点赞：{{xqlist.like}}</span>
   </div>
<!-- 评论列表 -->
<van-list
v-model="loading"
:finished="finished"
finished-text="没有更多了"
@load="onLoad"
>
 <div class="pllb" v-for="(item,index) in list" :key="index">
      <img :src="item.info.avatar" alt="">
        <li>{{item.info.nickname}}</li>
        <li>{{new Date(parseInt( item.create_time)).toLocaleString().replace(/:\d{1,2}$/,'')}}</li>
       <div class="pl">
        <h5>{{item.content}}</h5>
          <span><van-icon name="chat-o" :badge="item.reply_num" size="25" @click="hfpl(item._id)"/></span>
          <span><van-icon name="like-o" size="25"/></span>
          <!-- *************************回复评论列表 -->
          <div class="hfpllb" v-for="(item1,index) in hfpllb" :key="index">
            <img :src="item1.info.avatar" alt="">
            <li>{{item1.info.nickname}}</li>
            <li>{{new Date(parseInt( item1.create_time)).toLocaleString().replace(/:\d{1,2}$/,'')}}</li>
            <p>{{item1.content}}</p>
          </div>
       </div>
    </div>
</van-list>
   
<!-- 底部 -->
   <div class="dibu">
        <input type="text" placeholder="写评论"  v-model="inputtext">
        <van-icon name="chat-o" :badge="xqlist.comment" size="30" @click="pinglun"/>
        <van-icon name="like"  size="30" @click="undianzan"  color='red' v-if="xqlist.is_like"/> 
        <van-icon name="like-o"  size="30" @click="dianzan" v-else/>
        <van-icon name="star"  size="30"  v-if="xqlist.is_fav" color="red"/> 
        <van-icon name="star-o"  size="30" v-else/>
        <van-icon name="share-o"  size="30"/>

    </div>
  </div>
</template>

<script>
import "@/css/xiangqingcss.css"
export default {
    name:'xiangqing',
    data() {
        return {
            id:this.$route.query.id,
            xqlist:[],
            inputtext:'',
            list:[],
            limit:10,
            loading: false,
            finished: false,
            hfpllb:[],
            vals:'',
        }
    },
    created(){
      this.aaa();
     this.bbb();
    },
    methods:{
      aaa(){
        this.axios
          .post(
            "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_detail",
            {
                article_id:this.id, //文章ID
                user_id:localStorage.getItem("trip")//用户ID
            }
          )
          .then((res) => {
            console.log(res.data.data);
            this.xqlist=res.data.data
          });
      },
        fanhui(){
            this.$router.go(-1)
        },
        // ***************************发送点赞
        dianzan(){
          this.axios
          .post(
            " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/like",
            {
                user_id:localStorage.getItem("trip"), //用户ID
                article_id:this.id//点击的那条评论的ID
            }
          )
          .then((res) => {
            console.log(res.data);
            if(res.data.msg='点赞成功'){
              this.$toast('点赞成功');
            }
            this.aaa()
          });
          
        } ,
        // ******************************取消点赞
        undianzan(){
          this.axios
          .post(
            " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/unlike",
            {
                user_id:localStorage.getItem("trip"), //用户ID
                article_id:this.id//点击的那条评论的ID
            }
          )
          .then((res) => {
            console.log(res.data);
            if(res.data.msg='取消点赞成功'){
              this.$toast('取消点赞成功');
            }
            this.aaa()
          });
          
        } ,
        // ****************************发送评论
        pinglun(){
          this.axios.post(
            " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_comment",
            {
              user_id:localStorage.getItem("trip"), //用户ID
              article_id: this.id, //文章ID
              content:this.inputtext, //内容
              reply_comment_id: "", //回复的评论ID comment_type为1时有效  comment_type为0时可以不填
              comment_type: 0 //回复类型，0表示回复文章，1表示回复评论
            }
          )
          .then((res) => {
            console.log(res);
           if(res.data.msg=='评论成功'){
            this.inputtext=''
            this.onLoad()
           }
          });
        },
        dingyue(){ 
          this.axios.post(
            " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_fav",
            {
              user_id:localStorage.getItem("trip"), //用户ID
              article_id: this.id, //文章ID
            }
          )
          .then((res) => { 
            console.log(res.data);
            if(res.data.msg='收藏成功'){
              this.$toast('收藏成功');
            }
            this.aaa()
          });
        },
        dingyueyes(){
         
          this.axios.post(
            " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/remove_fav",
            {
              user_id:localStorage.getItem("trip"), //用户ID
              article_id: this.id, //文章ID
            }
          )
          .then((res) => { 
            console.log(res)
            if(res.data.msg='取消收藏成功'){
              this.$toast('取消收藏成功');
            }
            this.aaa()
          });
         
        },
        // ***************************评论列表
        onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        this.axios.post(
            " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_comment_list",
            {
              user_id:localStorage.getItem("trip"), //用户ID
              article_id: this.id, //文章ID
             skip:'0',
             limit:this.limit
            }
          )
          .then((res) => {
            console.log(res.data.data);
            this.list=res.data.data
          });

        // 加载状态结束
        this.loading = false;
          this.limit+=10
        // 数据全部加载完成
        if (this.list.length <=this.limit) {
          this.finished = true;
        }
      }, 1000);
    },
    // ********************回复评论
    hfpl(val){
      this.axios.post(
            " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_comment",
            {
              user_id:localStorage.getItem("trip"), //用户ID
              article_id: this.id, //文章ID
              content:this.inputtext, //内容
              reply_comment_id:val, //回复的评论ID comment_type为1时有效  comment_type为0时可以不填
              comment_type: 1 //回复类型，0表示回复文章，1表示回复评论
            }
          )
          .then((res) => {
            console.log(res);
            this.vals=val
            console.log(this.vals);
            console.log(this.id);
            this.bbb()
          });
    },
     // ********************************************回复评论列表
     bbb(){
      this.axios.post(
            " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_reply_list",
            {
              user_id:localStorage.getItem("trip"), //用户ID
              article_id: this.id, //文章ID
              content:this.inputtext, //内容
              reply_comment_id: this.vals, //回复的评论ID comment_type为1时有效  comment_type为0时可以不填
              comment_type: "1" //回复类型，0表示回复文章，1表示回复评论
            }
          )
          .then((res) => {
            console.log(res.data.data);
            this.hfpllb=res.data.data
          });
     }
  },
}
</script>

