<template>
  <div>
    <van-nav-bar
    title="浏览历史"
    left-arrow
    @click-left="onClickLeft"
    />
    <div v-for="(item,index) in historylist" :key="index"  @click="history(index)">
        <h3>用户名:{{item.author}}</h3>
        <li>分类名:{{item.cate_name}}</li>
        <li>内容:{{item.title}}</li>
        <li>发布时间:{{ new Date(parseInt(item.time)).toLocaleString().replace(/:\d{1,2}$/,'')}}</li>
        <img :src="item.imageSrc" alt="">
    </div>
  </div>
</template>

<script>
export default {
    name:'history',
    data() {
        return {
            uid:localStorage.getItem("trip"),
            historylist:[]
        }
    },
    methods:{
        onClickLeft() {
      this.$router.go(-1)
    },
    history(val){
        this.$router.push({
            path:'./xiangqing',
            query:{
                id:this.historylist[val]._id
            }
        })
    }
    },
    created(){
        this.axios.post(`https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_history_list?uid=${this.uid}&skip&limit`).then((res) => {
            console.log(res.data);
            this.historylist=res.data.data
          });
    }
}
</script>

