<template>
  <div class="hello">
    <div class="dingwei">
       <div class="top">
      <div class="input" @click="sousuo"> 
         <van-swipe style="height: 30px;" vertical :autoplay="1500" :show-indicators="false">
        <van-swipe-item>二手市场</van-swipe-item>
        <van-swipe-item>婚恋交友</van-swipe-item>
        <van-swipe-item>装修装饰</van-swipe-item>
        <van-swipe-item>明日头条</van-swipe-item>
        <van-swipe-item>今日头条</van-swipe-item>
        <van-swipe-item>劳力市场</van-swipe-item>
      </van-swipe>
    </div>
    </div>
    
    <van-tabs color="#4fc08d" v-model="active1" border @click="meaxios">
      <van-tab :title="item.name" v-for="(item,index) in rel" :key="index"></van-tab>
    </van-tabs>

    </div>
   

    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="myaxios"
      class="zj"
    >
      <div v-for="(item, index) in list" :key="index" class="mymjk" @click="myxq(index)">
        <li>{{ item.title }}</li>
        <li><img :src="item.imageSrc" alt=""/></li>
        <li> <span>作者：{{item.author}}</span> <span>评论:{{item.comment}} </span>时间：{{ new Date(parseInt( item.time)).toLocaleString().replace(/:\d{1,2}$/,'')}}</li>
        <li>{{ agoTime(item.time) }}</li>
      </div>
    </van-list>
  </div>
</template>

<script>
import { Col } from "vant";
export default {
  name: "index",
  data() {
    return {
      active1:0 ,
      list: [],
      rel: [],
      loading: false,
      finished: false,
      limit: 10,
      time:'',
      length:'',
      skip:0
    };
  },
  created() {
    this.axios
      .get(
        "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_cate_list"
      )
      .then((res) => {
        console.log(res.data.data);
        this.rel = res.data.data;
         this.axios
        .post(
          "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_list",
          {
            cate_id:this.rel[0]._id,
            skip: 0,
            limit: 10,
          }
        )
        .then((res) => {
          console.log(res.data.data);
          this.list = res.data.data; 
        });

      })
  },
  methods: {
    myaxios() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        this.axios
          .post(
            "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_list",
            {
              cate_id: this.rel[this.active1]._id,
              skip: this.skip,
              limit: this.limit,
            }
          )
          .then((res) => {
            console.log(res.data);
            this.list = res.data.data;
            this.length = res.data.count;
          });
        this.limit += 10;
        this.list.push(this.list.length + 10);

        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
      }, 1000);
    },
    meaxios(){
      this.myaxios()
      this.skip=0
      this.limit=10
    },
    myxq(val){
     this.$router.push({
      path:'./xiangqing',
      query: { 
        id:this.list[val]._id,
    }
     }) 
    },
    agoTime(dateTimeStamp) {
      dateTimeStamp = new Date(dateTimeStamp).getTime(); // 获取到时间的毫秒数
      let result = null;
      var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime(); //获取当前时间毫秒
      // console.log(now)
      var diffValue = now - dateTimeStamp; //时间差
      if (diffValue < 0) {
        return;
      }
      var minC = diffValue / minute; //计算时间差的分，时，天，周，月
      var hourC = diffValue / hour;
      var dayC = diffValue / day;
      var weekC = diffValue / week;
      var monthC = diffValue / month;
      if (monthC >= 1 && monthC <= 3) {
        result = " " + parseInt(monthC) + "月前";
      } else if (weekC >= 1 && weekC <= 3) {
        result = " " + parseInt(weekC) + "周前";
      } else if (dayC >= 1 && dayC <= 6) {
        result = " " + parseInt(dayC) + "天前";
      } else if (hourC >= 1 && hourC <= 23) {
        result = " " + parseInt(hourC) + "小时前";
      } else if (minC >= 1 && minC <= 59) {
        result = " " + parseInt(minC) + "分钟前";
      } else if (diffValue >= 0 && diffValue <= minute) {
        result = "刚刚";
      } else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth =
          datetime.getMonth() + 1 < 10
            ? "0" + (datetime.getMonth() + 1)
            : datetime.getMonth() + 1;
        var Ndate =
          datetime.getDate() < 10
            ? "0" + datetime.getDate()
            : datetime.getDate();
        var Nhour =
          datetime.getHours() < 10
            ? "0" + datetime.getHours()
            : datetime.getHours();
        var Nminute =
          datetime.getMinutes() < 10
            ? "0" + datetime.getMinutes()
            : datetime.getMinutes();
        var Nsecond =
          datetime.getSeconds() < 10
            ? "0" + datetime.getSeconds()
            : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
      }
      return result;
    },
    sousuo(){
      this.$router.push('./sousuo')
    }
  },
};
</script>

