<template>
  <div>
    <van-datetime-picker
  v-model="currentDate"
  type="date"
  title="选择年月日"
  :min-date="minDate"
  :max-date="maxDate"
  @confirm="sr"
/>
  </div>
</template>

<script>
export default {
    name:'shengri',
    data() {
    return {
      minDate: new Date(1990, 0, 1),
      maxDate: new Date(2022, 10, 1),
      currentDate: '',
    };
  },
  methods:{
    sr(){
        this.$router.push('./myzl')
        localStorage.setItem('birthday',this.currentDate.getFullYear() + "-" + (this.currentDate.getMonth() + 1) + "-" + this.currentDate.getDate())
    }
  }
}
</script>

