<template>
  <div>
    <van-nav-bar
  title="修改密码"
  left-text="返回"
  left-arrow
  @click-left="onClickLeft"
/>  
<van-field v-model="password" type="password" label="请输入旧密码" />
  <van-field v-model="newpassword" type="password" label="请输入新密码" />
  <button @click="btn">确认</button>
  </div>

</template>

<script>
export default {
    name:'rivisepassword',
    data() {
        return {
            password:'',
            newpassword:'',
        }
    },
    methods: {
    onClickLeft() {
     this.$router.go(-1)
    },
    btn(){
      this. axios.post("  https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/updatePwd",{
        oldPassword:this.password,
        newPassword:this.newpassword,
        uid:localStorage.getItem("trip")
            }).then((res) => {
                if(res.data.msg=='修改成功'){
                    this.$router.push('./login')
                    localStorage.clear()
                }else{
                    this.$toast(res.data.error.message)
                }
                console.log(res);
            })
    }
  },
  created(){
   
  }
}
</script>

<style scoped>
button{width: 100%;}
</style>