<template>
  <div>
    <van-nav-bar
  title="性别"
  left-text="返回"
  right-text="确定"
  left-arrow
  @click-left="onClickLeft"
  @click-right="onClickRight"
/>
    <p>请选择性别：</p>
    <van-radio-group v-model="radio">
        <van-radio name="男">男</van-radio>
        <van-radio name="女">女</van-radio>
      </van-radio-group>
  </div>
</template>

<script>
export default {
    name:'sex',
    data() {
    return {
      radio: '',
    };
  },
  methods: {
    onClickLeft() {
     this.$router.go(-1)
    },
    onClickRight() {
     this.$router.push({
        path:'./myzl',
        query:{
            sex1:this.radio
        }
     })
     localStorage.setItem('sex',this.radio)
    },
  },
}
</script>

