import Vue from 'vue'
import VueRouter from 'vue-router'
import index from '../views/index.vue'
import login from '../views/login.vue'
import fabu from '../views/fabu.vue'
import zhuce from '../views/zhuce.vue'
import xiangqing from '../views/xiangqing.vue'
import myfabu from '../views/myfabu.vue'
import sousuo from '../views/sousuo.vue'
import myzl from '../views/myzl.vue'
import name from '@/views/name.vue'
import shengri from '@/views/shengri.vue'
import sex from '@/views/sex.vue'
import rivisepassword from '@/views/rivisepassword.vue'
import forgetpassword from '@/views/forgetpassword.vue'
import collection from '@/views/collection.vue'
import history from '@/views/history.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'index',
    component: index,
    meta:{root:true}
  },
  {
    path: '/history',
    name: 'history',
    component: history,
  },
  {
    path: '/forgetpassword',
    name: 'forgetpassword',
    component: forgetpassword,
  },
  {
    path: '/collection',
    name: 'collection',
    component: collection,
  },
  {
    path: '/rivisepassword',
    name: 'rivisepassword',
    component: rivisepassword,
  },
  {
    path: '/sex',
    name: 'sex',
    component: sex
  },
  {
    path: '/shengri',
    name: 'shengri',
    component: shengri
  },
  {
    path: '/name',
    name: 'name',
    component: name
  },
  {
    path: '/myzl',
    name: 'myzl',
    component: myzl
  },
  {
    path: '/sousuo',
    name: 'sousuo',
    component: sousuo
  },
  {
    path: '/myfabu',
    name: 'myfabu',
    component: myfabu
  },
  {
    path: '/xiangqing',
    name: 'xiangqing',
    component: xiangqing
  },
  {
    path: '/zhuce',
    name: 'zhuce',
    component: zhuce,
    meta:{root:true}
  },
  {
    path: '/login',
    name: 'login',
    component: login,
    meta:{root:true}
  },
  {
    path: '/fabu',
    name: 'fabu',
    component: fabu,
    meta:{root:true}
  },
]
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
