<template>
  <div>
    <van-nav-bar
    title="忘记密码"
    left-text="返回"
    left-arrow
    @click-left="onClickLeft"
  />  
  <van-field v-model="text" type="text" label="请输入手机号" />
    <van-field v-model="newpassword" type="password" label="请输入新密码" />
    <van-field v-model="yzm" type="text" label="请输入验证码" />
    <button class="btn"  v-if="captcha" @click="captchas">发送验证码</button>
    <button class="btn"  v-else>倒计时：{{time}}</button>
    <button @click="yes" class="yes">确认</button>
  </div>
</template>

<script>
export default {
    name:"forgetpassword",
    data() {
        return {
            captcha:true,
            text:'',
            newpassword:"",
            yzm:'',
            time:10
        }
    },
    methods:{
        onClickLeft() {
     this.$router.go(-1)
    },
    captchas(){
        this.captcha=false;
        let dsq=setInterval(() => {
          this.time--
          if(this.time==0){
            this.captcha=true
            clearInterval(dsq)
            this.time=10
          }
        }, 1000);
        this.axios.post(" https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/sendSms",{
            mobile:this.text,
            type:'login'
        }
      ).then((res) => {
        console.log(res);
        console.log(res.data.data);
        this.$toast("验证码是"+res.data.data)
      })
    },
    yes(){
      this.axios.post(" https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/forget",{
            username:this.text,
            password:this.newpassword,
            vercode:this.yzm
        }
      ).then((res) => {
        console.log(res);
        if(res.data.msg=='密码重置成功'){
            this.$router.push('./login')
        }
      })
    }
    },
    
}
</script>

<style scoped>
.yes{width: 100%;}
</style>