<template>
  <div class="mjk">
    <div class="top">
        <span><i class="iconfont icon-fanhui"></i></span>
        <h3>注册/登入</h3>
    </div>
    <van-cell-group class="input">
        <van-field v-model="active" placeholder="请输入用户名" />
      </van-cell-group>
      <van-cell-group class="inputs">
        <van-field v-model="value" placeholder="请输入密码" />
      </van-cell-group>
      <van-cell-group class="inputs">
        <van-field v-model="yzm" placeholder="请输入验证码" />
      </van-cell-group>
      <button class="btn" @click="myyzm" v-if="btn">发送验证码</button>
      <button class="btn"  v-else>倒计时：{{time}}</button>
      <button class="end" @click="zc">注册</button>
      <p>如果有账号？去<span @click="dengru">登入</span></p>
  </div>
</template>

<script>
export default {
    name:'zhuce',
    data() {
        return {
            active:'',
            value:'',
            yzm:'',
            time:10,
            btn:true,
            tishi:''
        }
    },
    methods:{
      zc(){
        this.axios.post(" https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/reg",{"username":this.active,"password":this.value,"vercode":this.yzm}
      ).then((res) => {
        console.log(res.data.msg);
        if(this.active.length==11&&res.data.msg=='注册成功'&&this.value.length>=6){
          this.$toast('注册成功');
          localStorage.setItem('password',this.value)
          this.$router.push('./login')
         
      }else{
        this.$toast('请输入正确的手机号和密码')
      }
      })
      },
      myyzm(){
        this.btn=false;
        let dsq=setInterval(() => {
          this.time--
          if(this.time==0){
            this.btn=true
            clearInterval(dsq)
            this.time=10
          }
        }, 1000);
        this.axios.post("https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/sendSms",{"mobile":this.active,"type":"register"}
      ).then((res) => {
        console.log(res);
        console.log(res.data.data);
        this.tishi=res.data.data
        this.$toast("验证码是"+this.tishi)
      })
      },
      dengru(){
        this.$router.push('./login')
      }
    }
}
</script>

<style scoped>
p{text-align:center;}
p span{color:red}
.top{width: 100vw;height: 30px;background:#07c160 ;line-height: 30px;color:#fff;text-align: center;}
.top span{font-size: 20px;margin-left: 20px;float: left;color:#9FE7C1}
.top h3{text-align: center;font-size: 12px;}
.zhong button{width: 70px;height: 20px;border-radius: 100px;background: #EDEDED;border: 1px solid #ededed;float: right;position: relative;top:-25px;}
.end{width: 80vw;height: 30px;background: #07c160;margin: 0 auto;border: 1px solid #07c160;margin-left: 10vw;color:#fff;}
.btn{width: 90px;height: 30px;border-radius: 100px;float: right;position: relative;top: -35px;}
</style>