<template>
  <div>
    <van-nav-bar
    title="我的收藏"
    left-text="返回"
    left-arrow
    @click-left="onClickLeft"
  />
  <div v-for="(item,index) in mycollection" :key="index" class="mycollection">
    <h3 @click="mycollections(index)">用户名:{{item.author}}</h3>
    <li @click="mycollections(index)">分类名:{{item.cate_name}}</li>
    <li @click="mycollections(index)">内容:{{item.title}}</li>
    <li>发布时间:{{ new Date(parseInt(item.time)).toLocaleString().replace(/:\d{1,2}$/,'')}}<button @click="collection(index)">取消收藏</button></li>
    <img :src="item.imageSrc" alt="" @click="mycollections(index)">
</div>
  </div>
</template>

<script>
export default {
    name:'collection',data() {
        return {
            mycollection:[]
        }
    },
    methods:{
        onClickLeft(){
            this.$router.go(-1)
        },
        collection(val){
          this.axios.post(
            " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/remove_fav",
            {
              user_id:localStorage.getItem("trip"), //用户ID
              article_id: this.mycollection[val]._id, //文章ID
            }
          )
          .then((res) => { 
            console.log(res)
            this.collections()
          });
        },
        collections(){
          this.axios.post(" https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_fav_list",{
            user_id:localStorage.getItem("trip"),
            skip:0,
            limit:100000
        }
      ).then((res) => {
        console.log(res.data.data);
        this.mycollection=res.data.data
      })
    },
    mycollections(val){
      this.$router.push({
        path:"./xiangqing",
        query:{
          id:this.mycollection[val]._id
        }
      })
    }
    },
    created(){
       this.collections()
    }
}
</script>

