<template>
  <div>
    <form action="/">
        <van-search
          v-model="value"
          show-action
          background="#4fc08d"
          placeholder="请输入搜索关键词"
          @search="onSearch"
          @cancel="onCancel"
          @input="mjk"
        />
      </form>
      <ul v-for="(item,index) in list" :key="index">
        <li @click="lilist(index)"><i class="iconfont icon-sousuo">{{item.title}}</i></li>
      </ul>
      <div class="Search">
        <p>历史搜索<span>清除<i class="iconfont icon-shanchu"></i></span></p>
        <li>{{text[0].text}}</li>
      </div>
  </div>
</template>

<script>
export default {
    name:'sousuo',
    data() {
    return {
      value: '',
      list:[],
      text:localStorage.getItem('sezrch')
    };
  },
  methods: {
    onSearch() {
        this.mjk
    },
    mjk(){
      this.axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/search',{
                key_word:this.value,
                limit:10000
             }).then((res) => {
                    console.log(res.data.data);
                    this.list=res.data.data
                   
                  });
    },
    onCancel() {
      this.$router.go(-1)
    },
    lilist(val){ 
      let cunchu={
        value:this.index,
        text:this.value,
      }
       this.value=''
     localStorage.setItem('sezrch',JSON.stringify(cunchu)),
        this.$router.push({
            path:'./xiangqing',
            query:{
                id:this.list[val]._id,
                cate_id:this.list[val].cate_id,
            }
        })
    }
  },
created(){
console.log(this.text)
}
}
</script>

<style scoped>
ul{padding: 5px;}
ul li{width: 100%;border: 1px solid #ededed;line-height: 40px;}
.Search{width: 95%;padding: 5px;}
.Search span{float: right;}

</style>