<template>
    <div class="app">
        <div class="mjk" v-if="username===null">
         <div class="top">
            <span><i class="iconfont icon-fanhui"></i></span>
            <h3>注册/登入</h3>
         </div>
         <van-cell-group class="input">
             <van-field v-model="active" placeholder="请输入用户名" />
        </van-cell-group>
      <van-cell-group class="inputs">
        <van-field v-model="value" placeholder="请输入密码" />
      </van-cell-group>
      <button class="end" @click="dr">登录</button>
      <p style="margin-top:20px;">如果没有账号？去<span @click="login">注册</span>,<span @click="forget">忘记密码</span></p>
        </div>
        
    <div v-else class="xs">
      <ul>
            <li style="height:70px;line-height: 70px;">头像<img :src="mygrzl.avatar" alt=""></li>
            <li>用户名<span>{{mygrzl.nickname}}</span></li>
            <li>手机号<span>{{mygrzl.username}}</span></li>
            <li>生日<span>{{mygrzl.birthday}}</span></li>
            <li>性别<span>{{mygrzl.sex}}</span></li>
            <van-grid>
                <van-grid-item icon="graphic" text="修改资料"  @click="myzl"/>
                <van-grid-item icon="setting" text="修改密码" @click="xiugai" />
                <van-grid-item icon="phone-circle" text="联系我们" />
                <van-grid-item icon="warning" text="关于我们" />
                <van-grid-item icon="share" text="我的发布"  @click="myfabu" />
                <van-grid-item icon="star" text="我的收藏"  @click="collection" />
                <van-grid-item icon="underway" text="浏览历史"  @click="History"/>
              </van-grid>
        </ul>
       <button @click="refresh" style="width:90%;height:40px;background:#F5F7F9;margin-top:20px;margin-left:5%;">退出登入</button>
       <p style="text-align: center;">登入时间：{{time}}</p>
    </div>
    </div>
  
</template>

<script>
import '@/css/logincss.css'
export default {
    inject:['reload'],
    name:'login',
    data() {
        return {
            active:'',
            value:'',
            val:true,
            time:localStorage.getItem("time"),
            username:localStorage.getItem("username"),
            uid:localStorage.getItem("trip"),
            myfabu1:[],
            mygrzl:[],
            id:localStorage.getItem("trip")
        }
    },
    methods:{
        dr(){
            this.axios.post(" https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/login",{"username":this.active,"password":this.value}
            ).then((res) => {
                console.log(res);
                if(res.data.msg=='登录成功'){ 
                    this.mygrxx()
                    this.$toast('登入成功');
                    let aData = new Date();
                    let yy = new Date().getFullYear();
                    let mm = new Date().getMonth()+1;
                    let dd = new Date().getDate();
                    let hh = new Date().getHours();
                    let mf = new Date().getMinutes()<10 ? '0'+new Date().getMinutes() : new Date().getMinutes();
                    let ss = new Date().getSeconds()<10 ? '0'+new Date().getSeconds() : new Date().getSeconds();
                    aData = yy+'/'+mm+'/'+dd+' '+hh+':'+mf+':'+ss;
                    console.log(aData)  
                    console.log(res.data.uid)
                    this.$router.push("./")
                    localStorage.setItem('time',aData)
                    localStorage.setItem('trip',res.data.uid)
                    localStorage.setItem('username',res.data.username)
                    localStorage.setItem('token',res.data.token)
                    
                    
                }else{
                    this.$toast('账户或密码错误')
                    this.value=''
                }
            })
        },
        mygrxx(){
            this.axios.post(" https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/getuserInfo",{
                token:localStorage.getItem("token")
            }).then((res) => {
                
                console.log(res.data.userInfo);
                this.mygrzl=res.data.userInfo
                localStorage.setItem('_id',res.data.userInfo._id)
            })
        },
        myfabu(){
            this.$router.push('./myfabu')
    },
        login(){
            this.$router.push('./zhuce')
        },
        refresh(){
            localStorage.clear()
            this.reload()
        },
        myzl(){
            localStorage.setItem('birthday',this.mygrzl.birthday),
            localStorage.setItem('nickname',this.mygrzl.nickname),
            localStorage.setItem('sex',this.mygrzl.sex),
            localStorage.setItem('avatar',this.mygrzl.avatar),
            this.$router.push({
                path:'./myzl',
                query:{
                    myname:this.mygrzl.nickname
                }
            })
        },
        // ***********************修改密码
        xiugai(){
           this.$router.push('./rivisepassword')
        },
        // ***************忘记密码
        forget(){
            this.$router.push('./forgetpassword')
        },
        collection(){
            this.$router.push('./collection')
        },
        // *************************浏览历史
        History(){
            this.$router.push('./history')
        }
    },
    created(){
        this.mygrxx()
    }
}
</script>

<style scoped>
ul li{height: 40px;line-height: 40px;}
</style>