<template>
  <div>
    <van-nav-bar
      title="个人资料"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <ul>
      <li style="height: 90px">
        个人头像:<van-uploader
          v-model="fileList"
          multiple
          :max-count="1"
          :after-read="afterRead"
          class="uploader"
          ref="file"
        />
      </li>
      <li @click="names">
        发布姓名:<span><i class="iconfont icon-xiayiyeqianjinchakangengduo"></i></span><span>{{ nickname }}</span>
      </li>
      <li @click="shengri">
        生日<span><i class="iconfont icon-xiayiyeqianjinchakangengduo"></i></span><span>{{ birthday }}</span>
      </li>
      <li @click="sex">
        性别<span><i class="iconfont icon-xiayiyeqianjinchakangengduo"></i></span><span>{{ sex2 }}</span>
      </li>
    </ul>
    <button @click="baocun">保存</button>
  </div>
</template>

<script>
import { fromByteArray } from "ipaddr.js";
export default {
  name: "myzl",
  data() {
    return {
      uid: localStorage.getItem("trip"),
      birthday: localStorage.getItem("birthday"),
      sex2: localStorage.getItem("sex"),
      nickname: localStorage.getItem("nickname"),
      mynames: this.$route.query.myname,
      fileList: [{ url: localStorage.getItem("avatar") }], //默认是一个空数组
      imageData: {},
      isShow: false,
      showList: false,
      token: [],
      avatar: "",
    };
  },
  created() {
    this.tokens();
  },
  methods: {
    // *************上传文件
    afterRead(file) {
      // 此时可以自行将文件上传至服务器
      let param = new FormData();
      param.append("token", this.token);
      param.append("file", file.file);
      param.append("key", this.nanoid + file.file.name);
      this.axios
        .post("https://upload-z1.qiniup.com", param, {
          "Content-Type": "multipart/form-data",
        })
        .then((result) => {
          console.log("result: ", result);
          this.avatar = "http://toutiao.longxiaokj.com/" + result.data.key;
        })
        .catch((err) => {
          console.log("err: ", err);
        }); // console.log(file.file.name);
    },
    tokens() {
      this.axios
        .get(
          "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/upload/token"
        )
        .then((res) => {
          console.log(res.data.token);
          this.token = res.data.token;
        });
    },
    onClickLeft() {
      this.$router.push({
        path: "./login",
      });
    },
    // **************姓名
    names() {
      this.$router.push( "./name");
    },
    // ***************生日
    shengri() {
      this.$router.push("./shengri");
    },
    // ***************性别
    sex() {
      this.$router.push("./sex");
    },
    baocun() {
      this.axios
        .post(
          "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/editUserInfo",
          {
            uid: this.uid,
            avatar: this.avatar, //头像
            nickname: this.nickname,
            birthday: this.birthday,
            sex: this.sex2,
          }
        )
        .then((res) => {
          console.log(res);
          if ((res.data.msg = "更新成功")) {
            this.$router.push("./login");
          } else {
            this.$toast(res.data.msg);
          }
        });
    },
  },
};
</script>

<style scoped>
ul {
  padding: 5px;
}
ul li {
  width: 100%;
  border: 1px solid #ededed;
  height: 50px;
  line-height: 50px;
}
ul li span {
  float: right;
  font-size: 20px;
}
button {
  width: 80%;
  margin-left: 10%;
}
</style>