<template>
  <div>
    <div class="top">
        <h3>发布</h3>
    </div>
    <div class="a">
    <van-cell-group class="input">
        <van-field v-model="value1" placeholder="请输入用标题" />
      </van-cell-group>
      <van-field
          readonly
          clickable
          label="分类"
          :value="value"
          placeholder="选择分类名称"
          @click="showPicker = true"
        />
<van-popup v-model="showPicker" round position="bottom">
  <van-picker
    show-toolbar
    :columns="columns"
    @cancel="showPicker = false"
    @confirm="onConfirm"
  />
</van-popup>
      <van-cell-group class="inputs">
        <van-field v-model="values" placeholder="请输入内容" />
      </van-cell-group>

    </div>
    <van-uploader
    v-model="fileList"
    multiple
    :max-count="1"
    :after-read="afterRead"
    class="uploader"
    ref="file"
  />
    <button class="btn" @click="fabu">确定</button>

 
  </div>
</template>

<script>
export default {
    name:'fabu',
    data() {
    return {
        value1:'',
        values:'',
        rel:[],
        name:localStorage.getItem("_id"),
        uid:localStorage.getItem("trip"),
        fileList: [], //默认是一个空数组
      imageData: {},
      isShow: false,
      showList: false,
      token: [],
      avatar: "",
      value: '',
      showPicker: false,
      columns: [],
      id:''
    };
  },
  methods:{
    onConfirm(value,index) {
      this.value = value;
      this.showPicker = false;
      this.index=index
      this.id=this.rel[index]._id
    },
    tokens() {
      this.axios
        .get(
          "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/upload/token"
        )
        .then((res) => {
          console.log(res.data.token);
          this.token = res.data.token;
        });
    },
    afterRead(file) {
      this.tokens()
      // 此时可以自行将文件上传至服务器
      let param = new FormData();
      param.append("token", this.token);
      param.append("file", file.file);
      param.append("key", this.nanoid + file.file.name);
      this.axios
        .post("https://upload-z1.qiniup.com", param, {
          "Content-Type": "multipart/form-data",
        })
        .then((result) => {
          console.log("result: ", result);
          this.avatar = "http://toutiao.longxiaokj.com/" + result.data.key;
          console.log(this.avatar)
        })
        .catch((err) => {
          console.log("err: ", err);
        }); // console.log(file.file.name);
    },
    fabu(){
      this.axios.post("https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_article", {
            title: this.value1, //标题
            content:this.values, //内日
            cate_id:this.id, //分类ID
            cate_name: this.value, //分类名字
            author: this.name, //作者ID 也就是自己的用户ID
            author_id: this.uid, //也就是uid
            imageSrc:[this.avatar] 
            //上传的图片链接 
        }).then((res) => {
                console.log(res)
                if(res.data.msg=='成功'&&this.uid!=''){
                  this.$toast('发布成功');
                    this.$router.push('./')
                    this.value=''
                }else{
                  this.$toast('请先登入');
                }
            })
    },
  },
  created(){
    this.tokens()
    this.axios
      .get(
        "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_cate_list"
      )
      .then((res) => {
        console.log(res.data.data);
        this.rel = res.data.data;
        for(let i=0;i<this.rel.length;i++){
          this.columns.push(this.rel[i].name)
        }
      })
    }
}
</script>

<style scoped>
.btn{width: 80vw;height: 40px;background: #07c160;border: 1px solid #07c160;color:#fff;margin-top: 50px;margin-right:10%;}
.top{width: 100%;height: 30px;background:#07c160 ;line-height: 30px;color:#fff;text-align: center;}
.top h3{text-align: center;font-size: 12px;}
.a{width: 100%;background: #F5F7F9;}
.input{margin-top: 10px;}
.inputs{height: 100px;margin-top: 10px;}
select{width: 50%;height: 30px;}
</style>