<template>
  <div>
    <van-nav-bar
    title="个人资料"
    left-text="返回"
    left-arrow
    @click-left="onClickLeft"
  />
    <h3>请输入你的名称</h3>
    <input type="text" v-model="myname">
  </div>
</template>

<script>
export default {
    name:'names',
    data() {
      return {
        myname:''
      }
    },
    methods: {
    onClickLeft() {
      if(this.myname===''){
         this.myname=this.$route.query.mynames1
      }else{
        this.$router.push('./myzl')
      localStorage.setItem('nickname',this.myname)
      } 
    }
  }
}
</script>

<style scoped>
h3{font-weight: 400;}
input{border-radius: 10px;height: 30px;}
</style>